.control-buttons {
  display: flex;
  gap: 0;
  justify-content: space-between;
  width: min(var(--row-container-width), 110%);
  margin: 0 auto;
  padding: 10px 0px;
  border-radius: 18px;
  /* border: 2px solid rgba(0, 255, 255, 0.35); */
  /* background: rgba(0, 0, 0, 0.35); */
  /* box-shadow:
    0 0 10px rgba(0, 255, 255, 0.2),
    inset 0 0 8px rgba(0, 255, 255, 0.1); */
  box-sizing: border-box;
}

.control-btn {
  background: transparent;
  color: #00FFFF;
  border: 3px solid #00FFFF;
  border-radius: 20px;
  padding: 8px 24px;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.12s ease;
  box-shadow:
    0 0 4px rgba(0, 255, 255, 0.5),
    0 0 2px rgba(0, 255, 255, 0.3);
  position: relative;
  overflow: hidden;
  white-space: nowrap;
  will-change: transform, box-shadow;
  transform: translateZ(0);
}

.control-btn:hover {
  color: #00FFFF;
  border-color: #00FFFF;
  box-shadow:
    0 0 6px rgba(0, 255, 255, 0.7),
    0 0 3px rgba(0, 255, 255, 0.5);
  transform: translateY(-2px) translateZ(0);
}

.control-btn:active {
  color: #00FF00;
  border-color: #00FF00;
  border-width: 4px;
  box-shadow:
    0 0 6px rgba(0, 255, 0, 0.6),
    0 0 3px rgba(0, 255, 0, 0.4);
  transform: translateY(0) translateZ(0);
}

.reveal-btn {
  padding: 14px 20px;
  min-width: auto;
}

.reveal-btn svg {
  display: block;
  width: 28px;
  height: 28px;
}
