.app-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.phone-frame {
  display: contents;
}

.phone-screen {
  position: relative;
  z-index: 1;
}

.phone-chrome {
  display: none;
}

@media (min-width: 990px) {
  .app-container {
    align-items: flex-start;
    justify-content: flex-start;
  }

  .phone-frame {
    width: min(500px, 100vw);
    aspect-ratio: 9 / 19.5;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    position: relative;
    padding: 52px 20px 36px;
    border-radius: 75px;
    border: 8px solid rgba(0, 255, 255, 0.55);
    background: rgba(5, 12, 12, 0.4);
    box-shadow:
      0 0 40px rgba(0, 255, 255, 0.35),
      0 0 90px rgba(0, 255, 255, 0.25);
    /* Dynamic positioning to prevent overlap with promo section */
    /* Formula: center phone, but ensure min 40px from left and 40px gap before promo */
    /* Promo takes 410px from right (350px width + 60px margin) */
    /* Centered position: (100vw - 500px) / 2 */
    /* Max position to keep 40px gap: 100vw - 500px - 410px - 40px = 100vw - 950px */
    /* Minimum from left: 40px */
    margin-left: max(40px, min(calc((100vw - 500px) / 2), calc(100vw - 950px)));
  }

  .phone-screen {
    width: 100%;
    height: 100%;
  }

  .phone-chrome {
    display: block;
  }

  .phone-island {
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
    width: 170px;
    height: 36px;
    border-radius: 20px;
    background: #050909;
    border: 2px solid rgba(0, 255, 255, 0.25);
    box-shadow:
      inset 0 0 10px rgba(0, 0, 0, 0.6),
      0 0 6px rgba(0, 255, 255, 0.35);
  }

  .phone-button {
    position: absolute;
    width: 6px;
    height: 80px;
    border-radius: 6px;
    background: rgba(0, 255, 255, 0.4);
    box-shadow:
      0 0 10px rgba(0, 255, 255, 0.4),
      /* inset 0 0 6px rgba(0, 0, 0, 0.6); */
  }

  .phone-button--left-top {
    left: -15px;
    top: 180px;
    height: 80px;
  }

  .phone-button--left-mid {
    left: -15px;
    top: 280px;
    height: 80px;
    }

  .phone-button--right {
    right: -15px;
    top: 240px;
    height: 120px;
  }

  .content {
    padding: 20px 0;
  }
}

body {
  user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.content {
  max-width: 600px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  padding: 20px;
  position: relative;
  z-index: 1;
  --wheel-radius: 200px;
  --progress-ring-radius: calc(var(--wheel-radius) + 8px);
  --row-container-width: calc(var(--progress-ring-radius) * 2);
}

.main-body {
  transform: scale(var(--ui-scale, 1));
  transform-origin: top center;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 12px;
}

.header-content {
  text-align: center;
  flex: 1;
}

.title {
  font-size: 42px;
  font-weight: bold;
  color: #FFFFFF;
  margin: 0;
  text-shadow:
    0 0 20px rgba(0, 255, 255, 0.8),
    0 0 10px rgba(0, 255, 255, 0.5);
  letter-spacing: 0px;
}

.subtitle {
  font-size: 18px;
  color: #00FFFF;
  margin: 8px 0 0 0;
  text-shadow: 0 0 8px rgba(0, 255, 255, 0.6);
  opacity: 0.9;
}

.settings-row {
  width: min(var(--row-container-width), 100%);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0px auto 0px;
  padding: 8px 0px;
  border-radius: 18px;
  /* border: 2px solid rgba(0, 255, 255, 0.35); */
  /* background: rgba(0, 0, 0, 0.35); */
  /* box-shadow:
    0 0 10px rgba(0, 255, 255, 0.2),
    inset 0 0 8px rgba(0, 255, 255, 0.1); */
  box-sizing: border-box;
}

.settings-actions {
  display: flex;
  align-items: center;
  gap: 10px;
}

.help-btn {
  background: transparent;
  color: #00FFFF;
  border: 2px solid #00FFFF;
  border-radius: 50%;
  width: 54px;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 30px;
  font-weight: bold;
  cursor: pointer;
  box-shadow:
    0 0 8px rgba(0, 255, 255, 0.6),
    0 0 4px rgba(0, 255, 255, 0.4);
}

.help-btn:hover {
  box-shadow:
    0 0 12px rgba(0, 255, 255, 0.8),
    0 0 6px rgba(0, 255, 255, 0.6);
}

.help-dialog {
  background: rgba(0, 0, 0, 0.9);
  border-radius: 24px;
  padding: 28px 24px 24px;
  width: calc(100% - 60px);
  max-width: 440px;
  border: 3px solid rgba(0, 255, 255, 0.8);
  box-shadow:
    0 0 20px rgba(0, 255, 255, 0.5),
    0 0 12px rgba(0, 255, 255, 0.35);
  text-align: center;
  position: relative;
  animation: helpDialogFadeIn 0.3s ease-out;
}

@keyframes helpDialogFadeIn {
  from {
    opacity: 0;
    transform: scale(0.95) translateY(10px);
  }
  to {
    opacity: 1;
    transform: scale(1) translateY(0);
  }
}

.help-close-btn {
  position: absolute;
  top: 12px;
  right: 12px;
  background: transparent;
  border: 2px solid rgba(0, 255, 255, 0.8);
  color: #00FFFF;
  border-radius: 25%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  padding: 0;
  z-index: 1;
}

.help-close-btn svg {
  width: 16px;
  height: 16px;
}

.help-close-btn:hover {
  border-color: rgba(0, 255, 255, 0.8);
  background: rgba(0, 255, 255, 0.1);
  box-shadow:
    0 0 12px rgba(0, 255, 255, 0.6),
    0 0 6px rgba(0, 255, 255, 0.4);
  transform: rotate(90deg);
}

.help-header {
  text-align: center;
  margin-bottom: 18px;
}

.help-title {
  font-size: 24px;
  font-weight: bold;
  color: transparent;
  background: linear-gradient(135deg, #ffffff 0%, #00ffff 50%, #00ff99 100%);
  -webkit-background-clip: text;
  background-clip: text;
  margin-bottom: 6px;
  line-height: 1.2;
}

.help-subtitle {
  font-size: 14px;
  color: rgba(0, 255, 255, 0.7);
  font-weight: 500;
}

.help-instructions-frame {
  background: rgba(0, 0, 0, 0.6);
  border: 2px solid rgba(0, 255, 255, 0.7);
  border-radius: 16px;
  padding: 16px 18px;
  margin-bottom: 18px;
}

.help-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.help-item {
  display: flex;
  align-items: center;
  gap: 10px;
  text-align: left;
  padding: 6px 0;
}

.help-item-icon {
  font-size: 20px;
  line-height: 1;
  flex-shrink: 0;
  filter: drop-shadow(0 0 6px rgba(0, 255, 255, 0.3));
}

.help-item-content {
  flex: 1;
  font-size: 14px;
  color: rgba(255, 255, 255, 0.85);
  line-height: 1.5;
}

.help-item-title {
  font-weight: bold;
  color: #FFFFFF;
}

.help-cta-section {
  text-align: center;
  margin-top: 18px;
}

.help-cta-title {
  font-size: 16px;
  font-weight: bold;
  color: #FFFFFF;
  margin-bottom: 12px;
}

.store-links {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 14px;
  margin: 0;
}

.store-link {
  display: inline-flex;
  text-decoration: none;
  transition: all 0.2s ease;
  position: relative;
}

.store-link:hover {
  transform: translateY(-2px);
}

.store-link:hover .store-badge {
  box-shadow:
    0 0 16px rgba(0, 255, 255, 0.8),
    0 0 8px rgba(0, 255, 255, 0.6);
  border-color: rgba(0, 255, 255, 0.9);
}

.store-link-playstore {
  position: relative;
}

.store-badge {
  height: 52px;
  width: auto;
  border-radius: 10px;
  border: 2px solid rgba(0, 255, 255, 0.5);
  box-shadow:
    0 0 10px rgba(0, 255, 255, 0.4),
    0 0 6px rgba(0, 255, 255, 0.3);
  display: block;
  transition: all 0.2s ease;
}

.store-badge-overlay {
  position: absolute;
  bottom: 5px;
  right: 5px;
  font-size: 12px;
  font-weight: bold;
  color: #FF8DA1;
  background: rgba(0, 0, 0, 0.9);
  border: 2px solid rgba(255,141,161, 0.6);
  padding: 2px 8px;
  border-radius: 6px;
  text-shadow: 0 0 6px rgba(255, 141, 161, 0.9);
  box-shadow: 0 0 8px rgba(255, 141, 161, 0.7);
  white-space: nowrap;
  pointer-events: none;
  animation: badgePulse 2s ease-in-out infinite;
}

@keyframes badgePulse {
  0%, 100% {
    box-shadow: 0 0 24px rgba(255,141,161, 0.7);
  }
  50% {
    box-shadow: 0 0 32px rgba(255, 141, 161, 0.9);
  }
}
.footer {
  text-align: center;
  margin-top: 8px;
}

.reveal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}

.reveal-dialog {
  background: rgba(0, 0, 0, 0.9);
  border-radius: 24px;
  padding: 32px;
  max-width: 400px;
  width: calc(100% - 80px);
  text-align: center;
  border: 3px solid rgba(0, 255, 255, 0.8);
  box-shadow:
    0 0 20px rgba(0, 255, 255, 0.5),
    0 0 12px rgba(0, 255, 255, 0.35);
}

.reveal-icon {
  font-size: 48px;
  color: #00FFFF;
  text-shadow:
    0 0 12px rgba(0, 255, 255, 0.8),
    0 0 6px rgba(0, 255, 255, 0.6);
  margin-bottom: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.reveal-icon svg {
  width: 48px;
  height: 48px;
  display: block;
}

.reveal-title {
  font-size: 24px;
  font-weight: bold;
  color: #FFFFFF;
  margin-bottom: 16px;
}

.reveal-degrees {
  font-size: 36px;
  font-weight: bold;
  color: #00FFFF;
  text-shadow:
    0 0 8px rgba(0, 255, 255, 0.8),
    0 0 4px rgba(0, 255, 255, 0.6);
  padding: 16px;
  border-radius: 16px;
  border: 2px solid rgba(0, 255, 255, 0.7);
  background: rgba(0, 0, 0, 0.6);
  margin-bottom: 20px;
  font-family: "Courier New", monospace;
}

.reveal-close {
  background: transparent;
  border: 3px solid #00FFFF;
  color: #00FFFF;
  border-radius: 20px;
  padding: 12px 36px;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  box-shadow:
    0 0 8px rgba(0, 255, 255, 0.5),
    0 0 4px rgba(0, 255, 255, 0.3);
}

.reveal-close:hover {
  box-shadow:
    0 0 12px rgba(0, 255, 255, 0.7),
    0 0 6px rgba(0, 255, 255, 0.5);
}

.settings-dialog {
  background: rgba(0, 0, 0, 0.9);
  border-radius: 20px;
  padding: 24px 28px;
  width: calc(100% - 80px);
  max-width: 420px;
  border: 2px solid rgba(0, 255, 255, 0.7);
  box-shadow:
    0 0 18px rgba(0, 255, 255, 0.4),
    0 0 10px rgba(0, 255, 255, 0.25);
  transform: scale(var(--ui-scale, 1));
  transform-origin: center;
}

.settings-title {
  font-size: 20px;
  font-weight: bold;
  color: #FFFFFF;
  margin-bottom: 16px;
  text-align: center;
}

.settings-row-control {
  display: flex;
  align-items: center;
  gap: 12px;
}

.settings-slider {
  flex: 1;
  appearance: none;
  height: 6px;
  border-radius: 999px;
  background: rgba(0, 255, 255, 0.25);
  box-shadow: inset 0 0 6px rgba(0, 255, 255, 0.3);
  outline: none;
}

.settings-slider::-webkit-slider-thumb {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #00FFFF;
  box-shadow:
    0 0 10px rgba(0, 255, 255, 0.8),
    0 0 6px rgba(0, 255, 255, 0.6);
  border: 2px solid rgba(255, 255, 255, 0.6);
  cursor: pointer;
}

.settings-slider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #00FFFF;
  box-shadow:
    0 0 10px rgba(0, 255, 255, 0.8),
    0 0 6px rgba(0, 255, 255, 0.6);
  border: 2px solid rgba(255, 255, 255, 0.6);
  cursor: pointer;
}

.settings-length-value {
  min-width: 28px;
  text-align: center;
  color: #00FFFF;
  font-weight: bold;
  font-size: 18px;
  text-shadow:
    0 0 6px rgba(0, 255, 255, 0.6);
}

.settings-confirm {
  background: transparent;
  border: 2px solid #00FFFF;
  color: #00FFFF;
  border-radius: 16px;
  padding: 8px 18px;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  box-shadow:
    0 0 8px rgba(0, 255, 255, 0.5),
    0 0 4px rgba(0, 255, 255, 0.3);
}

.settings-confirm:hover {
  box-shadow:
    0 0 12px rgba(0, 255, 255, 0.7),
    0 0 6px rgba(0, 255, 255, 0.5);
}

.hint-text {
  color: rgba(255, 255, 255, 0.6);
  font-size: 14px;
  margin: 0;
}

/* Responsive design for mobile */
@media (max-width: 768px) {
  .content {
    max-width: 100%;
    padding: 16px;
  }

  .title {
    font-size: 32px;
  }

  .subtitle {
    font-size: 16px;
  }

  .help-dialog {
    width: calc(100% - 40px);
    padding: 24px 18px 20px;
  }

  .help-title {
    font-size: 20px;
  }

  .help-subtitle {
    font-size: 13px;
  }

  .help-instructions-frame {
    padding: 14px 16px;
  }

  .help-item {
    gap: 8px;
    padding: 5px 0;
  }

  .help-item-icon {
    font-size: 18px;
  }

  .help-item-content {
    font-size: 13px;
  }

  .help-cta-title {
    font-size: 15px;
    margin-bottom: 10px;
  }

  .store-links {
    gap: 10px;
  }

  .store-badge {
    height: 46px;
  }

  .store-badge-overlay {
    font-size: 9px;
    padding: 2px 5px;
  }
}

/* iPhone Pro size optimization */
@media (max-width: 430px) and (max-height: 932px) {
  .content {
    gap: 16px;
  }

  .title {
    font-size: 28px;
  }

  .subtitle {
    font-size: 14px;
  }

  .help-dialog {
    width: calc(100% - 32px);
    padding: 22px 16px 18px;
  }

  .help-header {
    margin-bottom: 14px;
  }

  .help-title {
    font-size: 19px;
  }

  .help-subtitle {
    font-size: 12px;
  }

  .help-instructions-frame {
    padding: 12px 14px;
    margin-bottom: 14px;
  }

  .help-item {
    padding: 4px 0;
  }

  .help-cta-section {
    margin-top: 14px;
  }

  .help-cta-title {
    font-size: 14px;
    margin-bottom: 8px;
  }

  .store-badge {
    height: 44px;
  }
}

/* App Promo Section - Right side on wide screens */
.app-promo-section {
  display: none;
}

@media (min-width: 990px) {
  .app-promo-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    position: fixed;
    right: 60px;
    bottom: 60px;
    width: 350px;
    max-height: 66vh;
    padding: 32px 28px;
    background: rgba(0, 0, 0, 0.35);
    border-radius: 28px;
    border: 3px solid rgba(0, 255, 255, 0.75);
    box-shadow:
      0 0 30px rgba(0, 255, 255, 0.5),
      0 0 18px rgba(0, 255, 255, 0.35);
    gap: 24px;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    cursor: default;
  }

  .app-promo-section.minimized {
    width: 80px;
    height: 80px;
    max-height: 80px;
    padding: 0;
    border-radius: 50%;
    gap: 0;
    cursor: pointer;
    justify-content: center;
    background: rgba(0, 0, 0, 0.7);
  }

  .app-promo-section.minimized:hover {
    transform: scale(1.1);
    border-color: rgba(0, 255, 255, 1);
    box-shadow:
      0 0 35px rgba(0, 255, 255, 0.8),
      0 0 20px rgba(0, 255, 255, 0.6);
  }

  .promo-minimize-btn {
    position: absolute;
    top: 12px;
    right: 12px;
    background: transparent;
    border: 0px solid rgba(0, 255, 255, 0.7);
    color: #00FFFF;
    border-radius: 50%;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    font-size: 28px;
    font-weight: 300;
    line-height: 1;
    transition: all 0.2s ease;
    z-index: 10;
    padding: 0;
    /* box-shadow:
      0 0 8px rgba(0, 255, 255, 0.4),
      0 0 4px rgba(0, 255, 255, 0.3); */
  }

  .promo-minimize-btn:hover {
    background: rgba(0, 255, 255, 0.15);
    border-color: rgba(0, 255, 255, 1);
    box-shadow:
      0 0 16px rgba(0, 255, 255, 0.7),
      0 0 10px rgba(0, 255, 255, 0.5);
    transform: scale(1.05);
  }

  .promo-minimized-icon {
    width: 56px;
    height: 56px;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .promo-minimized-icon img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .promo-title {
    font-size: 26px;
    font-weight: bold;
    color: transparent;
    background: linear-gradient(135deg, #ffffff 0%, #00ffff 50%, #00ff99 100%);
    -webkit-background-clip: text;
    background-clip: text;
    margin: 0;
    text-align: center;
    line-height: 1.3;
  }

  .promo-icon {
    width: 100px;
    height: 100px;
    border-radius: 22px;
    overflow: hidden;
    border: 3px solid rgba(0, 255, 255, 0.7);
    box-shadow:
      0 0 20px rgba(0, 255, 255, 0.6),
      0 0 12px rgba(0, 255, 255, 0.4);
  }

  .promo-icon img {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: cover;
  }

  .promo-stores {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    width: 100%;
  }

  .promo-store-link {
    display: inline-flex;
    text-decoration: none;
    transition: all 0.3s ease;
    position: relative;
  }

  .promo-store-link:hover {
    transform: translateY(-3px);
  }

  .promo-store-link:hover .promo-store-badge {
    box-shadow:
      0 0 24px rgba(0, 255, 255, 0.9),
      0 0 14px rgba(0, 255, 255, 0.7);
    border-color: rgba(0, 255, 255, 1);
  }

  .promo-store-badge {
    height: 80px;
    width: auto;
    border-radius: 17px;
    border: 3px solid rgba(0, 255, 255, 0.6);
    box-shadow:
      0 0 16px rgba(0, 255, 255, 0.5),
      0 0 10px rgba(0, 255, 255, 0.4);
    display: block;
    transition: all 0.3s ease;
  }

  .promo-store-link-playstore {
    position: relative;
  }

  .promo-store-badge-overlay {
    position: absolute;
    top: 8px;
    right: 8px;
    font-size: 12px;
    font-weight: bold;
    color: #FF8DA1;
    background: rgba(0, 0, 0, 0.95);
    border: 2px solid rgba(255, 141, 161, 0.7);
    padding: 3px 10px;
    border-radius: 8px;
    text-shadow: 0 0 8px rgba(255, 141, 161, 0.9);
    box-shadow: 0 0 12px rgba(255, 141, 161, 0.8);
    white-space: nowrap;
    pointer-events: none;
    animation: promoBadgePulse 2s ease-in-out infinite;
  }

  @keyframes promoBadgePulse {
    0%, 100% {
      box-shadow: 0 0 28px rgba(255, 141, 161, 0.8);
    }
    50% {
      box-shadow: 0 0 38px rgba(255, 141, 161, 1);
    }
