    height: 56px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.7);
    border: 3px solid rgba(0, 255, 255, 0.6);
    box-shadow:
      0 0 14px rgba(0, 255, 255, 0.5),
      0 0 8px rgba(0, 255, 255, 0.3);
    transition: all 0.3s ease;
