    overflow: hidden;
  }

  .promo-social-link:hover {
    transform: translateY(-4px) scale(1.1);
    border-color: rgba(0, 255, 255, 1);
    box-shadow:
      0 0 24px rgba(0, 255, 255, 0.8),
      0 0 14px rgba(0, 255, 255, 0.6);
    background: rgba(0, 255, 255, 0.1);
  }

  .promo-social-link img {
    width: 55px;
    height: 55px;
    display: block;
    object-fit: contain;
  }
}
